import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox,Drawer,Collapse, Row, Popconfirm} from 'antd';
import moment from 'moment';
import './index.less';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms,coaches} from '../../../entry/index/routes/IndexPage';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('show-course-list-drawer', (rec,courseId)=>{
        util.ajaxJson(urlPrefix+'TrainOperation?opt=getCourseInfo',{courseId},res=>{
            if(res.success){
                let courseInfo=res.data;
                this.setState({visible:true,rec,courseInfo,courseId,modifyCourseTime:undefined});
            }
        });
        
    });
  	this.state={
      visible:false,
      rec:{},
      courseInfo:{
          course:{},
          records:[]
      }
  	};
  }

  deleteStudentRec=r=>{
    util.ajaxJson(urlPrefix+'TrainOperation?opt=deleteStudentRecord',{studentId:r.studentId,id:r.id},res=>{
      if(res.success){
        message.success('');
        this.refreshCourseInfo();
      }
    });
  }
  deleteStudentRechargeBillRec=r=>{
    Modal.confirm({
      title:'',
      content:'',
      onOk:()=>{
        util.ajaxJson(urlPrefix+'TrainOperation?opt=deleteStudentRechargeBillRec',{id:r.id},res=>{
          if(res.success){
            message.success('');
            this.refreshCourseInfo();
          }
        });
      }
    });
  }
  refreshCourseInfo=()=>{
    let {courseId}=this.state;
    util.ajaxJson(urlPrefix+'TrainOperation?opt=getCourseInfo',{courseId},res=>{
        if(res.success){
            let courseInfo=res.data;
            this.setState({courseInfo});
        }
    });
    emitter.emit(`queryStudentTable`, undefined, true);
  }

  deleteCourse=async ()=>{
    let {courseId}=this.state;
    let billRes=await util.ajaxJsonAsync(urlPrefix+'BillOperation?opt=queryBill',{domain:'',billIdPrefix:'stuRecharge'+courseId+'-'});
    if(!billRes.success)return;
    if(billRes.data.totalItem>0){
      Modal.confirm({
        title:'',
        content:`${billRes.data.totalItem}`,
        onOk:()=>this.doDeleteCourse(billRes.data.data.map(it=>it.id))
      });
    }else{
      this.doDeleteCourse(billRes.data.data.map(it=>it.id));
    }
  }

  doDeleteCourse=(ids)=>{
    let {courseId}=this.state;
    util.ajaxJson(urlPrefix+'TrainOperation?opt=deleteStudentCourse',{courseId,ids},res=>{
      if(res.success){
        message.success('');
        this.setState({visible:false});
        emitter.emit(`queryStudentTable`, undefined, true);
      }
    });
  }

  onClose=()=>{
      this.setState({visible:false});
  }

  modifyCourseTimes=()=>{
    let {courseInfo,modifyCourseTime} = this.state;
    let {course}=courseInfo;
    if(!modifyCourseTime||modifyCourseTime==course.times)return;
    if(modifyCourseTime<(course.times-course.remainTimes))return message.warn('');
    let remainTimes=course.remainTimes+(modifyCourseTime-course.times);
    Modal.confirm({
      title:"",
      content:`${course.times}${modifyCourseTime}${course.remainTimes}${remainTimes}`,
      onOk:()=>{
        util.ajaxJson(urlPrefix+'TrainOperation?opt=updateCourse',{courseId:course.id,times:modifyCourseTime,remainTimes},res=>{
          if(res.success)message.success('');
          this.refreshCourseInfo();
        });
      }
    });
  }

  render() {
    let {visible,rec,courseInfo,modifyCourseTime} = this.state;
    let {course,records,bills}=courseInfo;
    let coach=coaches.filter(it=>it.id==course.coachId)[0];
  	return (<Drawer title={`${rec.name}- ${coach&&coach.name}`} getContainer={()=>document.querySelector('.Student-container')} className="student-course-drawer" visible={visible} width={800} placement="right" onClose={this.onClose} destroyOnClose >
          <Collapse defaultActiveKey={['1','2','3']}>
            <Collapse.Panel header="" key="1">
                <Row>
                    <Form.Item label="">{rec.name}</Form.Item>
                    <Form.Item label="">{coach&&coach.name}</Form.Item>
                </Row>
                <Row>
                    <Form.Item label="()">{course.remainAmount}/{course.amount}</Form.Item>
                    <Form.Item label="">{course.remainTimes}/{course.times}&nbsp;<Popconfirm getPopupContainer={()=>document.querySelector('.student-course-drawer .ant-drawer-body')} title={<><h3></h3><Form.Item label=""><InputNumber value={modifyCourseTime?modifyCourseTime:course.times} onChange={e=>{
                      this.setState({modifyCourseTime:e});
                    }} min={0} max={1000} precision={0}/></Form.Item></>} onConfirm={this.modifyCourseTimes}><a></a></Popconfirm></Form.Item>
                </Row>
                <Row>
                    <Form.Item label="">{course.remark}</Form.Item>
                    <Form.Item label="">{course.type}</Form.Item>
                </Row>
                <Row>
                    <Form.Item label="">{util.formatDate(course.gmtCreate)}</Form.Item>
                    <Form.Item label="">{course.operator}</Form.Item>
                </Row>
            </Collapse.Panel>

            <Collapse.Panel header="" key="2">
                <Table pagination={false} rowKey={r=>r.id} dataSource={records} size="small" columns={[{key:'studyTime',dataIndex:'studyTime',title:'',render:v=>util.formatDate(v)},
          {key:'coachId',dataIndex:'coachId',title:'',render:(v,r)=>coaches.filter(it=>it.id==v)[0].name+(v===r.mainCoachId?'':'')},
          {key:'consumeTimes',dataIndex:'consumeTimes',title:''},
          {key:'remainTimes',dataIndex:'remainTimes',title:'()',render:(v,r,idx)=>{
            let consumeTimes=0;
            for(let i=0;i<records.length;i++){
              if(idx<=i){
                consumeTimes+=records[i].consumeTimes;
              }
            }
            let snapTimes=course.times-consumeTimes;
            return `${snapTimes}/${course.times}`;
          }},
          {key:'consumeAmount',dataIndex:'consumeAmount',title:'()'},
          {key:'snapAmount',dataIndex:'snapAmount',title:'()',render:(v,r,idx)=>{
            let consumeAmount=0;
            for(let i=0;i<records.length;i++){
              if(idx<=i){
                consumeAmount+=records[i].consumeAmount;
              }
            }
            return `${course.amount-consumeAmount}/${course.amount}`;
          }},
          {key:'operator',dataIndex:'operator',title:''},
          {key:'opt',dataIndex:'opt',title:'',render:(v,r)=><span><Popconfirm getPopupContainer={()=>document.querySelector('.student-course-drawer')} title="" onConfirm={()=>this.deleteStudentRec(r)}><a></a></Popconfirm></span>}]}/>
            </Collapse.Panel>

            <Collapse.Panel header="" key="3">
                <Table pagination={false} rowKey={r=>r.id} dataSource={bills} size="small" columns={[{key:'chargeTime',dataIndex:'chargeTime',title:'',render:v=>util.formatDate(v)},
          {key:'times',dataIndex:'times',title:''},
          {key:'amount',dataIndex:'amount',title:'()'},
          {key:'operator',dataIndex:'operator',title:''},
          {key:'opt',dataIndex:'opt',title:'',render:(v,r)=><span><Popconfirm getPopupContainer={()=>document.querySelector('.student-course-drawer')} title="" onConfirm={()=>this.deleteStudentRechargeBillRec(r)}><a></a></Popconfirm></span>}]}/>
            </Collapse.Panel>
          </Collapse>

          <Button type="primary" onClick={()=>emitter.emit('rechargeStudentModal',rec,courseInfo.course)}></Button>
          {records&&records.length>0&&<Tooltip getPopupContainer={()=>document.querySelector('.student-course-drawer')} title="" ><Button disabled type="danger"></Button></Tooltip>}
          {!records||records.length<=0&&<Popconfirm getPopupContainer={()=>document.querySelector('.student-course-drawer')} title="" onConfirm={this.deleteCourse}><Button type="danger"></Button></Popconfirm>}
          
      </Drawer>);
  }
}

export default BizComponent;