import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox,Drawer,Collapse, Row, Popconfirm} from 'antd';
import moment from 'moment';
import ReactToPrint from 'react-to-print';
import './index.less';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms,coaches,name,tenantConfig} from '../../../entry/index/routes/IndexPage';
import DatePick from '../../../components/select/datePick';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('show-coach-settlement-drawer', (rec)=>{
      let {formValues}=this.state;
      formValues.timeVal='30';
      this.setState({formValues});
      let rangeValue=[moment(moment().month(moment().month()).startOf('month').valueOf()).format('YYYY-MM-DD HH:mm:ss'),moment(moment().month(moment().month()).endOf('month').valueOf()).format('YYYY-MM-DD HH:mm:ss')];
      this.doLoadData(rec,rangeValue);
        
    });
  	this.state={
      visible:false,
      studentRecords:[],
      studentNames:[],
      formValues:{
        timeVal:'30'
      },
      rec:{}
  	};
  }

  doLoadData=(rec,rangeValue)=>{
    this.rangeValue=rangeValue;
    util.ajaxJson(urlPrefix+'TrainOperation?opt=queryStudentRecord',{coachId:rec.id,startTime:rangeValue[0],endTime:rangeValue[1]},res=>{
      if(res.success){
          let studentRecords=res.data.data,studentNames=[];
          studentRecords.sort((a,b)=>{
            if(a.studentId-b.studentId == 0){
              return a.id-b.id;
            }
            return a.studentId-b.studentId;
          });
          studentRecords.forEach(it=>{
            if(studentNames.indexOf(it.studentName)==-1)studentNames.push(it.studentName);
          });
          this.setState({visible:true,rec,studentRecords,studentNames});
      }
   });
  }

  doSettlement=()=>{
    let {studentRecords,rec} = this.state;
    let hide=message.loading('...');
    util.ajaxJson(urlPrefix+'TrainOperation?opt=doSettlement',{coachId:rec.id,recIds:studentRecords.map(it=>it.id),total:this.getTotal(),roomId:studentRecords[0].roomId},res=>{
      hide();
      if(res.success){
        message.success('');
        this.doLoadData(rec,this.rangeValue);
      }
    });
  }

  getTotal=(totalFlag)=>{
    let {studentRecords} = this.state;
    let total=Math.round(studentRecords.filter(it=>totalFlag?true:it.billed!=1).map(this.getSettleAmount).reduce((a,b)=>a+b,0));
    return total;
  }

  getSettleAmount=r=>{
    let rate=tenantConfig.coachRate;
    if(r.mainCoachId==r.coachId){
      rate=tenantConfig.majorCoachRate;
    }
    return r.consumeAmount*rate/100;
  }

  onClose=()=>{
      this.setState({visible:false});
  }

  render() {
    let {visible,rec,studentRecords,formValues,studentNames} = this.state;
    let total=this.getTotal(true),toSettleAmount=this.getTotal(false);
  	return (<Drawer title={`${rec.name}`} visible={visible} width={800} className="settlement-course-drawer" placement="left" onClose={this.onClose} destroyOnClose >
          <div className="settlement-course-print" ref={el=>this.printEl=el}>
            <Collapse defaultActiveKey={['1','2']}>
              <Collapse.Panel header="" key="1" showArrow={false} className="settle-info-collapse">
                  <Row>
                      <Form.Item label="">{rec.name}</Form.Item>
                      <Form.Item label="">{total}{toSettleAmount>0&&<span> ({toSettleAmount})</span>}</Form.Item>
                  </Row>
                  <Row>
                      <Form.Item label=""><DatePick modeType="select" validValues={['30','-30','100']} value={formValues.timeVal} onChange={(rangeValue,timeVal)=>{
                        let {formValues}=this.state;
                        formValues.timeVal=timeVal;
                        this.setState({formValues});
                        if(!rangeValue)return;
                        this.doLoadData(rec,rangeValue);
                      }}/></Form.Item>
                      <Form.Item label="">{name}</Form.Item>
                  </Row>
                  <Row>
                      <Form.Item label="" className="students">{studentNames.join(', ')}</Form.Item>
                  </Row>
              </Collapse.Panel>
              <Collapse.Panel header={` ${studentRecords.length}`} key="2" showArrow={false}>
                  <Table pagination={false} rowKey={r=>r.id} dataSource={studentRecords} size="small" columns={[
            {key:'studentName',dataIndex:'studentName',title:''},
            {key:'studyTime',dataIndex:'studyTime',title:'',render:v=>util.formatDate(v)},
            {key:'consumeTimes',dataIndex:'consumeTimes',title:''},
            {key:'consumeAmount',dataIndex:'consumeAmount',title:'()'},
            {key:'rate',dataIndex:'rate',title:'',render:(v,r)=>r.mainCoachId==r.coachId?`${tenantConfig.majorCoachRate}%()`:`${tenantConfig.coachRate}%`},
            {key:'settleAmount',dataIndex:'settleAmount',title:'()',render:(v,r)=>{
              return this.getSettleAmount(r);
            }},
            {key:'billed',dataIndex:'billed',title:'',render:(v,r)=>v==1?'':''},
            {key:'operator',dataIndex:'operator',title:''},
            ]}/>
              </Collapse.Panel>
            </Collapse>
            <Form.Item label="" className="total-amount">{total}</Form.Item>
          </div>
          <ReactToPrint documentTitle={`${rec.name}`}
            trigger={() => {
              return <Button type="primary"></Button>;
            }}
            content={() => this.printEl}
          />
          <Button disabled={studentRecords.length==0 || toSettleAmount==0} type="primary" onClick={()=>Modal.confirm({title:"",content:`${toSettleAmount}${rec.name}`,onOk:this.doSettlement})}></Button>
          
      </Drawer>);
  }
}

export default BizComponent;