import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,InputNumber,Popover,Icon,Select,Popconfirm, Switch, DatePicker} from 'antd';
import './index.less';
import moment from 'moment';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs, coaches} from '../../../entry/index/routes/IndexPage';
import ClassStudentListDrawer from './classStudentListDrawer';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on('rich-editor-save-'+'classDesc',({content,rec})=>{
      util.ajaxJson(urlPrefix+'TrainOperation?opt=updateClass',{rec:{id:rec.id,introduction:content}},res=>{
        emitter.emit(`query${this.config.key}Table`,undefined,true);
        if(res.success)message.success('');
      });
    });
    this.config = {
      title:'',
      key:'Class',
      dialog:{width:1060},
      queryCond:{
      conditions:[[{key:'name'}]],
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInForm:true,
          notInTable:true,
          label:'id'
        },{
          key:'name',
          width:'6%',
          rules:[{required: true, message: ''}],
          label:''
        },{
          key:'coachId',
          width:'6%',
          rules:[{required: true, message: ''}],
          enums:coaches.map(it=>({key:it.id+'',text:it.name})),
          label:''
        },{
          key:'price',
          width:'6%',
          type:'custom',
          notInTable:true,
          getComponent:props=><InputNumber {...props}/>,
          rules:[{required: true, message: ''}],
          label:'()'
        },{
          key:'introduction',
          width:'20%',
          notInForm:true,
          render:(v,r)=>{
            return <div><a onClick={()=>emitter.emit('open-rich-editor-drawer', {key:'classDesc',content:v, title:'',rec:r})}></a><br/><div dangerouslySetInnerHTML={{__html:v}}></div></div>;
          },
          label:''
        },{
          key:'recordCount',
          width:'6%',
          notInForm:true,
          render:(v,r)=>{
            return <a onClick={()=>emitter.emit('show-class-student-drawer',r)}>{v?v:0}</a>;
          },
          label:''
        },{
          key:'startTime',
          width:'10%',
          render:(v,r)=>util.formatDate(v,'yyyy-MM-dd'),
          rules:[{required: true, message: ''}],
          type:'custom',
          getComponent:props=><DatePicker {...props}/>,
          label:''
        },{
          key:'endTime',
          width:'10%',
          render:(v,r)=>util.formatDate(v,'yyyy-MM-dd'),
          rules:[{required: true, message: ''},{validator:(v,values)=>{
            if(v&&values.startTime&&values.startTime.valueOf()>v.valueOf()){
              return false;
            }
            return true;
          },message:''}],
          type:'custom',
          getComponent:props=><DatePicker {...props}/>,
          label:''
        },{
          key:'project',
          width:'6%',
          enums:[{key:'',text:''}],
          label:''
        },{
          key:'operator',
          width:'6%',
          notInForm:true,
          label:''
        },{
          key:'gmtModified',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:''
        }
      ],
      operationPrefix:record=>{
        return (<span><a onClick={()=>{
          emitter.emit('show-class-student-drawer',record);
        }}></a></span>);
      },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {project:''};
        },
        beforeModify:formData=>{
          formData.startTime=moment(formData.startTime);
          formData.endTime=moment(formData.endTime);
          return formData;
        },
        beforeSubmit:formData=>{
          return formData;
        },
        afterSubmit:()=>{
          emitter.emit('init-user-info');
        }
      },
      table:{
        bordered:true,
        size:'small',
        expandedRowKeys:()=>this.state.expandedRowKeys,
        expandIcon:(pops)=>{
          let {expandedRowKeys}=this.state;
          let expanded=expandedRowKeys.indexOf(pops.record.id)>-1;
          if(!pops.record.recordCount)return;
          return <Icon type={expanded?"minus-square":"plus-square"} onClick={()=>{
            let {expandedRowKeys}=this.state;
            if(expanded){
              expandedRowKeys=[];
            }else{
              expandedRowKeys=[];
              expandedRowKeys.push(pops.record.id); 
            }
            this.setState({expandedRowKeys},this.refreshExpandRow);
          }}/>
        },
        expandedRowRender:(record, index, indent, expanded)=>{
          let {recDs}=this.state;
          return <Table className="student-rec-table" columns={[
          {key:'studentName',dataIndex:'studentName',title:''},
          {key:'phone',dataIndex:'phone',title:''},
          {key:'amount',dataIndex:'amount',title:'()'},
          {key:'operator',dataIndex:'operator',title:''},
          ]} rowKey={r=>r.id} dataSource={recDs.data} onChange={(recPagination)=>{
            this.setState({recPagination},()=>this.refreshExpandRow(true));
          }} pagination={recDs.totalItem<=10?false:{...this.state.recPagination,total:recDs.totalItem}} size="small"/>;
        },
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'TrainOperation?opt=queryClass'},
        updateInterface:isAdmin?{func:urlPrefix+'TrainOperation?opt=updateClass'}:undefined,
        insertInterface:isAdmin?{func:urlPrefix+'TrainOperation?opt=insertClass'}:undefined,
        deleteInterface:isAdmin?{func:urlPrefix+'TrainOperation?opt=deleteClass',params:{id:'id'}}:undefined
      }
    };
    this.state={
      expandedRowKeys:[],
      recDs:{totalItem:0}
    };
  }

  refreshExpandRow=(retainPage)=>{
    let {expandedRowKeys,recPagination}=this.state;
    if(!recPagination)recPagination={current:1,pageSize:10};
    if(!retainPage)recPagination.current=1;
    if(expandedRowKeys&&expandedRowKeys.length>0){
      emitter.emit(`get${this.config.key}QueryCondForm`,(queryParam={})=>{
        let {time,onlyTrain}=queryParam;
        util.ajaxJson(urlPrefix+'TrainOperation?opt=queryClassStudents',{classId:expandedRowKeys[0],currentPageNum:recPagination.current,pageSize:recPagination.pageSize},res=>{
          this.setState({recDs:res.data});
        });
      });
    }
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
       <ClassStudentListDrawer/>
      </div>
    );
  }
}