import React,{Component} from 'react';
import {message,Input,Modal,Form,Select,Tag} from 'antd';
import {util,emitter} from '../../common/common';
import {Consumer} from './index';
import EditableField from '../../components/pops/editableField';
export default class BizComp extends Component {

	onChange=()=>{
		let {formValues} = this.parentContainer.state;
		this.props.onChange(formValues);
	}

	render(){
		return <Consumer>
			{parentContainer=>{
				this.parentContainer = parentContainer;
				let {formValues} = parentContainer.state;// formValues()ognl
				return <div className="contact-c">
					  {formValues.contacts&&formValues.contacts.map((contact,idx)=>{
					  	return <Tag key={'contact'+idx} className="contact-item" closable onClose={(e)=>{
							e.stopPropagation();
							e.preventDefault();
							Modal.confirm({
								title:'',
								content:'',
								onOk:()=>{
									formValues.contacts.splice(idx,1);
									parentContainer.setState({formValues});
									this.onChange();
								}
							});
						  }}>
					  		<Form.Item label="">
						  		<Input size="small" onBlur={this.onChange} onPressEnter={this.onChange} {...util.getSubField.bind(parentContainer)(`contacts[${idx}]`,'contactName',contact)}/>
					  		</Form.Item>
					  		<Form.Item label="">
						  		<Select size="small" {...util.getSubField.bind(parentContainer)(`contacts[${idx}]`,'contactType',contact,{rules:[{validator:(v)=>{
						  			return !!v;
						  		},message:""}],onChange:this.onChange})}>
						  			<Select.Option value=""></Select.Option>
						  			<Select.Option value=""></Select.Option>
									<Select.Option value=""></Select.Option>
									<Select.Option value=""></Select.Option>
						  		</Select>
					  		</Form.Item>
							<Form.Item label="">
								<EditableField onBlur={(v)=>{
									contact.mobile_no=v;
									this.onChange();
								}} onCall={this.props.onCall} value={contact.mobile_no} contactType={contact.contactType}/>
							</Form.Item>
							<Form.Item label="">
						  		<Input size="small" onBlur={this.onChange} onPressEnter={this.onChange} {...util.getSubField.bind(parentContainer)(`contacts[${idx}]`,'contactLevel',contact)}/>
					  		</Form.Item>
					  	</Tag>;
					  })}
					  <a onClick={()=>{
					  	let {contacts} = formValues;
					  	if(!contacts){
					  		contacts=[];
					  		formValues.contacts = contacts;
					  	}
					  	contacts.push({});
					  	parentContainer.setState({formValues});
					  }}></a>
				</div>;
			}}
		</Consumer>;
	}
}
