import React, { Component } from 'react';
import {Button, Drawer, Modal, Popconfirm} from 'antd';
import { emitter, util } from '../../../../common/common';
import './index.less';
export default class BizComponent extends Component {
    constructor(...args) {
        super(...args);
        emitter.on('open-rich-editor-drawer',({key,title,width=1000,content='',rec})=>{
            this.setState({visible:true,key,width,title,rec},()=>{
                if(!window.ue){
                    setTimeout(()=>{
                        window.ue=window.UE.getEditor('uedit-container');
                        window.ue.ready(()=>{
                            window.ue.setContent(content)
                        });
                    },200);
                    
                }else{
                    window.ue.setContent(content);
                }
            });
            
        });
        this.state={
            visible:false
        };
    }
    onSave=()=>{
        emitter.emit('rich-editor-save-'+this.state.key,{content:window.ue.getContent(),rec:this.state.rec});
        this.onClose();
    }
    onClose=()=>{
        this.setState({visible:false});
    }
    onCloseConfirm=()=>{
        Modal.confirm({
            title:"",
            content:"",
            onOk:this.onClose
        });
    }
    render() {
        let {title,width,visible}=this.state;
        return <>
        <Drawer title={title} visible={visible} maskClosable={false} width={width} className="ueditor-drawer" placement="right" onClose={this.onCloseConfirm}>
            <script id="uedit-container" name="content" type="text/plain" style={{height:'calc(100vh - 300px)'}}></script>
            <Popconfirm title="" onConfirm={this.onSave}><Button type="primary"></Button></Popconfirm>
            <Button onClick={this.onCloseConfirm}></Button>
        </Drawer>
        </>;
    }
}