import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox,Drawer,Collapse, Row, Popconfirm} from 'antd';
import moment from 'moment';
import './index.less';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms,coaches,name,tenantConfig} from '../../../entry/index/routes/IndexPage';
import SelectTable from '../../../components/select/selectTable';
import '../../game/game/index.less';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('show-class-student-drawer', (rec)=>{
      this.doLoadData(rec);
        
    });
  	this.state={
      visible:false,
      formValues:{
        records:[]
      },
      rec:{}
  	};
  }

  doLoadData=(rec)=>{
    util.ajaxJson(urlPrefix+'TrainOperation?opt=queryClassStudents',{classId:rec.id},res=>{
      if(res.success){
          let records=res.data.data;
          records&&records.forEach(it=>it.id=it.id+'');
          if(!records||records.length==0)records=[{amount:rec.price},{amount:rec.price},{amount:rec.price}];
          let {formValues}=this.state;
          formValues.records=records;
          this.setState({visible:true,rec,formValues});
      }
   });
  }

  onSave=()=>{
    let {rec,formValues}=this.state;
    let {records}=formValues;
    records=records.filter(it=>it.id);
    util.ajaxJson(urlPrefix+'TrainOperation?opt=saveClassStudents',{classId:rec.id,records},res=>{
      if(res.success){
        message.success(`${res.data}`);
        emitter.emit('queryClassTable',undefined,true);
        this.onClose();
      }
    });
  }

  onClose=()=>{
    this.setState({visible:false});
  }
  onCloseConfirm=()=>{
      Modal.confirm({
          title:"",
          content:"",
          onOk:this.onClose
      });
  }

  onRowClick=(add,i)=>{
    let {formValues,rec}=this.state;
    let {records}=formValues;
    if(add){
      records.splice(i+1,0,{amount:rec.price});
    }else{
      if(records.length==1)return message.warn('');
      records.splice(i,1);
    }
    this.setState({formValues});
  }
  onRowChange=(v,r,idx)=>{
    let {formValues}=this.state;
    let {records}=formValues;
    if(records.filter(it=>it.id==v).length>0){
      message.warn(`${r.name}`);
      return false;
    }
    records[idx].phone=r?r.phone:undefined;
    this.setState({formValues});
    return true;
  }
  getSelected=(idx)=>{
    let {formValues}=this.state;
    let {records}=formValues;
    let selecteds=[];
    for(let i=0;i<records.length;i++){
      if(i!=idx&&records[i].id){
        selecteds.push(records[i].id+'');
      }
    }
    return selecteds;
  }

  render() {
    let {visible,rec,formValues} = this.state;
    let coach=coaches.filter(it=>it.id==rec.coachId)[0];
    let validCount=formValues.records.filter(it=>it.id).length;
  	return (<Drawer title={`${rec.name}`} visible={visible} width={800} className="class-students-drawer" placement="left" onClose={this.onCloseConfirm} destroyOnClose maskClosable={false}>
          <div className="settlement-course-print" ref={el=>this.printEl=el}>
            <Collapse defaultActiveKey={['1','2']}>
              <Collapse.Panel header="" key="1" showArrow={false} className="settle-info-collapse">
                  <Row>
                      <Form.Item label="">{rec.name}</Form.Item>
                      <Form.Item label="">{coach&&coach.name}</Form.Item>
                  </Row>
                  <Row>
                      <Form.Item label="">{util.formatDate(rec.startTime,'yyyy-MM-dd')}</Form.Item>
                      <Form.Item label="">{util.formatDate(rec.endTime,'yyyy-MM-dd')}</Form.Item>
                  </Row>
                  <Row>
                      <Form.Item label="()">{rec.price}</Form.Item>
                      <Form.Item label="()">{formValues.records.filter(it=>it.amount&&it.id).map(it=>it.amount).reduce((a,b)=>a+b,0)}</Form.Item>
                  </Row>
              </Collapse.Panel>
              <Collapse.Panel header={` ${validCount}`} key="2" showArrow={false}>
                <Table className="form-table" columns={[
                    {key:'opt',title:<Icon type="usergroup-delete" />,width:'64px',render:(v,r,i)=>
                      <div className="table-opt-col"><span className="idx">{i+1}</span><span className="btns show-off-i"><Button icon="plus" size="small" shape="round" onClick={()=>this.onRowClick(true,i)}></Button><Button icon="minus" size="small" shape="round" onClick={()=>this.onRowClick(false,i)}></Button></span></div>},
                    {key:'id',dataIndex:'id',title:'',width:'30%',render:(v,r,idx)=>
                        <SelectTable {...util.getSubField.call(this,`records[${idx}]`,'id',formValues.records[idx])} onSelected={(v,r)=>this.onRowChange(v,r,idx)} className={v?'':"show-off-i"} placeholder="" valueKey="id" nameKey="name" columns={[
                          {key:'name',title:'',width:'120px'},
                          {key:'phone',title:'',width:'120px'},
                          ]} excludeValues={this.getSelected(idx)} urlSuffix="TrainOperation?opt=queryStudent" initRecords={formValues.records.filter(it=>it.id&&it.name)} searchKey="namePhone" allowClear={false}/>},
                    {key:'phone',dataIndex:'phone',title:'',width:'30%'},
                    {key:'amount',dataIndex:'amount',title:'()',width:'20%',render:(v,r,idx)=><span className={v?'':"show-off-i"}><InputNumber placeholder="" precision={0} {...util.getSubField.call(this,`records[${idx}]`,'amount',r)}/></span>}
                    ]}
                rowKey={(r,id)=>id} size="small" dataSource={formValues.records} pagination={false}/>
              </Collapse.Panel>
            </Collapse>
            <Popconfirm title={`${validCount}`} onConfirm={this.onSave}><Button type="primary"></Button></Popconfirm>
            <Button onClick={this.onCloseConfirm}></Button>
          </div>
          
      </Drawer>);
  }
}

export default BizComponent;