import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popconfirm,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';
import MoneyField from '../../common/fields/money';
import PhoneField from '../../common/fields/phone';
import Coach from '../../common/fields/coach';
import MyUpload from '../../common/fields/upload';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('modifyStudentModal', (rec)=>{
      let formValues={...rec};
      formValues.roomId=rec.roomId+'';
      delete formValues.amount;
      delete formValues.courses;
      delete formValues.recordCount;
      delete formValues.remark;
      this.setState({visible:true,formValues,rec});
      
    });
  	this.state={
      formValues:{},
      visible:false
  	};
  }

  handleOk=()=>{
    util.validateFields.call(this,async values=>{
      util.ajaxJson(urlPrefix+'TrainOperation?opt=updateStudent', {rec:values}, res=>{
        if(res.success){
          message.success(`!`);
          this.handleCancel();
        }
      });
      
    });
  }

  handleCancel=()=>{
    emitter.emit(`queryStudentTable`, undefined, true);
    this.setState({visible:false});
  }

  render() {
    let {visible,formValues} = this.state;
  	return (<Modal title={``} className="modal-form" visible={visible} width={1200} destroyOnClose onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Popconfirm key="1" title="" onConfirm={this.handleOk}>{!this.state.loading&&<Button type="primary" size="large">
        
     </Button>}</Popconfirm>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            
         </Button>]
      }>
          <Form className="add-member-modal">
            <Form.Item className="" label="" required>
                <PhoneField form={this} />
            </Form.Item>
            <Form.Item className="memberName-f" label="" required>
                <Input {...util.getField.call(this,'name',{rules:[{required:true,message:''}]})} />
            </Form.Item>
            <Form.Item className="" label="">
              <MyUpload message="" quality={0.07} onLoading={loading=>this.setState({loading})} {...util.getField.call(this,'avatar')}/>
            </Form.Item>
            <Form.Item className="" label="" required>
              <Select {...util.getField.bind(this)('roomId',{rules:[{required:true,message:''}]})}>
                {rooms.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{it.name}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            
          </Form>
      </Modal>);
  }
}

export default BizComponent;