import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Tabs, Form, Select, Popconfirm, InputNumber} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';

import QueryCond from '../../../components/common/table/queryCond';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on(`menu-clicked-sys/config`,()=>{
      this.initTenantConfig();
    });
    this.initTenantConfig()
    this.state={
        config:{},
        formValues:{}
    };
  }
  initTenantConfig=()=>{
    util.ajaxJson(urlPrefix+'SysUserFramework?opt=getTenantConfig',{},res=>{
        let config=res.data||{};
        if(!config.billCates)config.billCates=['','',''];
        if(!config.majorCoachRate)config.majorCoachRate=55;
        if(!config.coachRate)config.coachRate=50;
        let formValues={...config};
        this.setState({config,formValues});
    });
  }

  render() {
    let {config}=this.state;
    return (
      <div className="sys-config-c">
       <Tabs defaultActiveKey="1">
            <Tabs.TabPane tab="" key="1">
                <Form.Item label="" required>
                    <Select mode="tags" placeholder="" value={config.billCates} onChange={v=>{
                        config.billCates=v;
                        this.setState({config});
                    }}>
                        {config.billCates&&config.billCates.map(it=><Select.Option key={it} value={it}>{it}</Select.Option>)}
                    </Select>
                </Form.Item>
                <Form.Item label="" required>
                  <div><InputNumber {...util.getField.call(this,'majorCoachRate',{rules:[{required:true,message:''}]})} precision={0} min={1} max={100}/>%</div>
                  <div><InputNumber {...util.getField.call(this,'coachRate',{rules:[{required:true,message:''}]})} precision={0} min={1} max={100}/>%</div>
                </Form.Item>
            </Tabs.TabPane>
        </Tabs>
        <Popconfirm title="" onConfirm={()=>{
          let {config,formValues}=this.state;
          config = {...config,...formValues};
            util.ajaxJson(urlPrefix+'SysUserFramework?opt=saveTenantConfig',{config},res=>{
                if(res.success){
                    message.success('');
                    emitter.emit('init-user-info');
                }
            });
        }}><Button type="primary"></Button></Popconfirm>
      </div>
    );
  }
}